import React from 'react';
import { useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  TextField,
  Typography,
  LinearProgress,
} from '@material-ui/core';

import {
  GuestLayout,
} from '@nvdunginest/emis-mui';

import {
  useTypedSelector,
  authActions,
  AppDispatch,
} from '../core';
import { ChangePasswordModel } from '../core/accountService';

const useStyles = makeStyles((theme) => ({
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function MustChangePassword(): JSX.Element {
  const classes = useStyles();
  const {
    auth,
  } = useTypedSelector(s => s);
  const dispatch: AppDispatch = useDispatch();

  const [model, setModel] = React.useState<ChangePasswordModel>({
    oldPassword: '',
    newPassword: '',
    confirmPassword: '',
  });

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setModel({ ...model, [event.target.name]: event.target.value });
  };

  const handleSubmit = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    event.preventDefault();
    const { oldPassword, newPassword, confirmPassword } = model;
    if (oldPassword && newPassword && confirmPassword) {
      dispatch(authActions.changePassword(model));
    }
  };

  return (
    <GuestLayout>
      <Typography variant="h6" color="primary" align="center">
        Đổi mật khẩu lần đầu đăng nhập hoặc sau khi khôi phục mật khẩu gốc
      </Typography>
      <form className={classes.form} noValidate>
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          label="Mật khẩu cũ"
          type="password"
          name="oldPassword"
          value={model.oldPassword}
          onChange={handleChange}
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          label="Mật khẩu mới"
          type="password"
          name="newPassword"
          value={model.newPassword}
          onChange={handleChange}
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          label="Xác nhận mật khẩu mới"
          type="password"
          name="confirmPassword"
          value={model.confirmPassword}
          onChange={handleChange}
        />
        {auth.isLoading
          ? <LinearProgress />
          : (
            <>
              {auth.isError && <Typography color="error" variant="subtitle2">{auth.errorMessage}</Typography>}
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={handleSubmit}
              >
                Đổi mật khẩu
              </Button>
            </>
          )}
      </form>
    </GuestLayout>
  );
}
