import React from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  TextField,
  FormControlLabel,
  Checkbox,
  Typography,
  LinearProgress,
  Grid,
  Link as LinkMUI,
} from '@material-ui/core';

import {
  GuestLayout,
} from '@nvdunginest/emis-mui';

import {
  getUserSubscription,
  base64Encode,
} from '../configs/notification';

import {
  useTypedSelector,
  authActions,
  AppDispatch,
} from '../core';
import { LoginModel } from '../core/accountService';

const useStyles = makeStyles((theme) => ({
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function Login(): JSX.Element {
  const classes = useStyles();
  const {
    auth,
  } = useTypedSelector(s => s);
  const dispatch: AppDispatch = useDispatch();

  const [model, setModel] = React.useState<LoginModel>({
    number: '',
    password: '',
    remember: false,
    endpoint: '',
    p256dh: '',
    auth: '',
  });

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setModel({ ...model, [event.target.name]: event.target.value });
  };

  const handleChecked = (event: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
    setModel({ ...model, remember: checked });
  };

  const handleSubmit = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    event.preventDefault();
    const { number, password } = model;
    if (number && password) {
      getUserSubscription()
        .then(subscription => {
          const data: LoginModel = {
            number: model.number,
            password: model.password,
            remember: model.remember,
            endpoint: subscription?.endpoint as string,
            p256dh: base64Encode(subscription?.getKey('p256dh') as ArrayBuffer),
            auth: base64Encode(subscription?.getKey('auth') as ArrayBuffer),
          };
          dispatch(authActions.login(data));
        })
        .catch(() => {
          const data: LoginModel = {
            number: model.number,
            password: model.password,
            remember: model.remember,
            endpoint: '',
            p256dh: '',
            auth: '',
          };
          dispatch(authActions.login(data));
        });
    }
  };

  return (
    <GuestLayout>
      <form className={classes.form} noValidate>
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          label="Mã nhân viên"
          name="number"
          autoComplete="number"
          value={model.number}
          onChange={handleChange}
          autoFocus
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          name="password"
          label="Mật khẩu"
          type="password"
          value={model.password}
          onChange={handleChange}
          autoComplete="current-password"
        />
        <FormControlLabel
          control={(
            <Checkbox
              checked={model.remember}
              onChange={handleChecked}
              name="remember"
              color="primary"
            />
          )}
          label="Ghi nhớ ?"
        />
        {auth.isLoading
          ? <LinearProgress />
          : (
            <>
              {auth.isError && <Typography color="error" variant="subtitle2">{auth.errorMessage}</Typography>}
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={handleSubmit}
              >
                Đăng nhập
              </Button>
              <Grid container>
                <Grid item xs>
                  <LinkMUI component={Link} to="/reset-password-request" variant="body2">
                    Quên mật khẩu?
                  </LinkMUI>
                </Grid>
              </Grid>
            </>
          )}
      </form>
    </GuestLayout>
  );
}
