import React from 'react';
import { Route, Switch } from 'react-router-dom';

import {
  askUserPermission,
  isPushNotificationSupported,
  userConsent,
} from './configs/notification';

import ResetPasswordRequest from './pages/ResetPasswordRequest';
import ResetPassword from './pages/ResetPassword';
import Login from './pages/Login';
import Private from './Private';

export default function App(): JSX.Element {
  React.useEffect(() => {
    if (isPushNotificationSupported && userConsent !== 'granted') {
      askUserPermission();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Switch>
      <Route path="/reset-password-request" component={ResetPasswordRequest} />
      <Route path="/reset-password/:resetPasswordToken" component={ResetPassword} />
      <Route path="/login" component={Login} />
      <Route path="/" component={Private} />
    </Switch>
  );
}
